import React from 'react';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
// margin m, padding p, top t, bottom b, left s, right e, 위아래 y, 좌우 x 
// 글자크기 fs-1, 흰색 text-white 회색 text-muted, 파랑 text-primary 초록 text-success 하늘색 text-info 노랑색 text-warning 빨간색 text-danger
// font-weight fw-bold
// 텍스트 줄간격 lh-lg
import { navJsonData } from './menujson';


function Header() {

	const logostyle = {
		fontSize: "25px",
		fontWeight: "bold",
		color: "#ddffaa"
	}

	const menuShow = (mItems) => { 
		return mItems.map( 
			(item, index) => { 
				if (item.submenu) { 
					return ( 
						//             서브메뉴의 부모
						<NavDropdown className="bg-primary" title={ item.title } key={index}> 
							{menuShow( item.submenu )} 
						</NavDropdown> 
					); 
				} else { 
					return ( 
							//서브매뉴의 item 설정    박스 바탕색  박스크기자동   글자색
							<Nav.Link  className="bg-primary dropdown-item text-light" href={ item.url } key={index}> {item.title} 
							</Nav.Link> 
					); 
				} 
			} 
		); 
	}; 
	return ( 
		// {/* header의 nav bar */}
		<Navbar className="navbar navbar-expand-md fixed-top navbar-dark bg-primary p-2 ps-4" > 
			<i className='fab fa-accusoft mx-2 fs-2 text-info' />
			<Navbar.Brand href="#home"> 
				<h1 className="navbar-brand" style={logostyle}> 
						<b>Envi. Catalyst</b> 
				</h1> 
			</Navbar.Brand> 
			<Navbar.Toggle/> 
			<Navbar.Collapse> 
				<Nav className="me-auto" > 
						{menuShow( navJsonData )} 
				</Nav> 
			</Navbar.Collapse> 
		</Navbar> 
	); 
}
export default Header;
